<template>
	<div id="Service">
		<headerclass></headerclass>
		<div class="secBody">
            <div class="secBoss">
                <!-- 上 -->
                <div class="secUP">
                    <p v-if='$store.state.adminuser.name'>用户名: <span>{{$store.state.adminuser.name}}</span></p>
                    <p v-else></p>
                </div>
                <!-- 下 -->
                <div class="secTable">
                    <!-- 标题 -->
                    <div class="sTheader">
                        <p>安全信息</p>
                    </div>
                    <!-- 输入框 -->
                    <div class="sTinput">
                        <div class="INInput">
                            <label>当前密码:</label>
                            <input type="password" placeholder="输入原密码" v-model="password">
                        </div>
                        <div class="INInput">
                            <label>新密码:</label>
                            <input type="password" placeholder="输入新密码" v-model="uword">
                        </div>
                        <div class="INInput">
                            <label>确认密码:</label>
                            <input type="password" placeholder="再次输入新密码" v-model="omord">
                        </div>
                    </div>
                    <!-- 按钮 -->
                    <div class="sTBtn">
                        <a href="javascript:;" @click="modify">确认修改</a>
                    </div>
                </div>
                <!-- 按钮 -->
                <div class="lastBtn">
                    <a href="javascript:;" @click="soulert" class="opento">返回主页</a>
                    <a href="javascript:;" @click="backio" class="opende">返回主页</a>
                </div>
            </div>
        </div>
	</div>
</template>

<script>
    import {changePassword} from "@/network/admin";
    const sha256 = require("js-sha256").sha256;
    import headerclass from "@views/Charging/header/header";
	export default {
		name:"Service",
		data() {
            return {
                 password: '',
                 uword: '',
                 omord: '',
                 isajax:true
            }
        },
		components: {
            headerclass,
		},
		created: function () {
            this.peroiong();
        },
        activated:function(){
            this.peroiong();
        },
		methods: {
			modify(){
				var upwd = this.password;
				var uword =this.uword;
				var omord = this.omord;
		        if (upwd == "") {
		            layer.msg('请输入密码!',{
		                        icon: 2,
		                        time:1500
		                    });
		            return false;
		        }

		        if (uword == "") {
		            layer.msg('请输入新密码!',{
		                        icon: 2,
		                        time:1500
		                    });
		            return false;
		        }
		        if (omord == "") {
		            layer.msg("请重新输入新密码!",{
		                        icon: 2,
		                        time:1500
		                    });
		            return false;
		        }
		        if (uword != omord) {
		            layer.msg('两次密码输入不一致，请重新输入!',{
		                        icon: 2,
		                        time:1500
		                    });
		            return false;
		        }
                var data ={
                    userName: this.$store.state.adminuser.name,
                    oldPassword:sha256(this.password),
                    newPassword:sha256(this.omord),
                }
                changePassword(data).then(res => {
                    console.log(res)
                    if (res.code== 0) {
                        var user={'name':''};
                        this.$store.commit('setdataadminuser',user)
                        layer.msg("修改成功！", {
                            icon: 1,
                            time: 1500
                        });
                            this.password='',
                            this.uword='',
                            this.omord='',
                        this.$router.push("/Charging/Login")
                    } else if(res.code !== 0){
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });

                    }
                })
			},
           soulert(){
                this.$router.push("/Charging/Administration/QueryAdminist")
            },
            backio(){
                this.$router.go(-1);
            },
            peroiong: function(){
                this.perionth = this.$store.state.permissions.permissions;
                if(this.perionth.length == 1){
                    $(".opende").show();
                    $(".opento").hide()
                }
                if(this.perionth.length == 2){
                    $(".opende").show()
                    $(".opento").hide()
                }
                if(this.perionth.length == 3){
                    $(".opende").show()
                    $(".opento").hide()
                }
                if(this.perionth.length == 4){
                    $(".opende").hide()
                    $(".opento").show()
                }
            },
		}
	}
</script>

<style>
	@import "~@assets/css/user/common.css";
	@import "~@assets/css/user/password.css";
</style>